    >
      <router-view></router-view>
    </transition>
    <div class="avatar">
      <img
        src="../assets/images/profile_pic.jpg"
        alt="Photo"
        class="avatar__photo"
      />
      <div class="follow-layout">
        <app-follow />
      </div>

      <h1 class="title">Sachin Sahu</h1>
      <h1 class="designation">Full stack Developer</h1>
      <div class="icon-box">
        <div class="icon-box__details">
          <svg class="icon-box__icon">
            <use xlink:href="../assets/icons/sprite.svg#icon-envelop" />
          </svg>
          <span class="icon-box__text">sachinr147@gmail.com</span>
        </div>
        <div class="icon-box__details">
          <svg class="icon-box__icon">
            <use xlink:href="../assets/icons/sprite.svg#icon-phone" />
          </svg>
          <span class="icon-box__text">+91 9325454230</span>
        </div>
        <div class="icon-box__details">
          <svg class="icon-box__icon">
            <use xlink:href="../assets/icons/sprite.svg#icon-location" />
          </svg>
          <span class="icon-box__text">402-A, Block-B,Hyderabad, India</span>
        </div>
        <!-- <button class="btn btn-animate btn-blue">Hire me</button> -->
      </div>
    </div>
    <div id="particle"></div>
  </div>
</template>
<script>
import Follow from "../components/Follow";
export default {
  name: "Profile",
  components: {
    appFollow: Follow,
  },
  data() {
    return {
      options: {
        particles: {
          number: {
            value: 110,
            density: {
              enable: true,
              value_area: 800,
            },
          },
          color: {
            value: "#0ca6bb",
          },
          shape: {
            type: "circle",
            stroke: {
              width: 0,
              color: "#000000",
            },
            polygon: {
              nb_sides: 5,
            },
            image: {
              src: "img/github.svg",
              width: 100,
              height: 100,
            },
          },
          opacity: {
            value: 0.5,
            random: false,
            anim: {
              enable: false,
              speed: 1,
              opacity_min: 1,
              sync: true,
            },
          },
          size: {
            value: 12,
            random: true,
            anim: {
              enable: true,
              speed: 10,
              size_min: 0.5,
              sync: false,
            },
          },
          line_linked: {
            enable: true,
            distance: 150,
            color: "#7f6f6f",
            opacity: 0.6,
            width: 1.5,
          },
          move: {
            enable: true,
            speed: 2,
            direction: "none",
            random: false,
            straight: false,
            out_mode: "out",
            bounce: false,
            attract: {
              enable: false,
              rotateX: 600,
              rotateY: 1200,
            },
          },
        },
        interactivity: {
          detect_on: "canvas",
          events: {
            onhover: {
              enable: true,
              mode: "grab",
            },
            onclick: {
              enable: true,
              mode: "push",
            },
            resize: true,
          },
          modes: {
            grab: {
              distance: 140,
              line_linked: {
                opacity: 1,
              },
            },
            bubble: {
              distance: 400,
              size: 40,
              duration: 2,
              opacity: 8,
              speed: 3,
            },
            repulse: {
              distance: 200,
              duration: 0.4,
            },
            push: {
              particles_nb: 4,
            },
            remove: {
              particles_nb: 2,
            },
          },
        },
        retina_detect: true,
      },
    };
  },
  methods: {
    setBackground() {
      window.particlesJS("particle", this.options);
    },
  },
  mounted() {
    this.setBackground();
  },
};
</script>
<style lang="scss" scoped>
@import "../assets/sass/abstract/variable.scss";
@import "../assets/sass/layouts/profilelayout.scss";
@import "../assets/sass/components/avatar.scss";
@import "../assets/sass/components/button.scss";
.icon-box {
  border-top: 2px solid#c1c1c1;
  padding-bottom: 2rem;
