import Vue from "vue";
import VueRouter from "vue-router";
import About from "./components/main_components/About";
import Blog from "./components/main_components/Blog";
import ContactUs from "./components/main_components/ContactUs";
import Project from "./components/main_components/Project";
import Portfolio from "./components/main_components/Portfolio";
import BDB from "./components/main_components/BDB";
import Inpixon from "./components/main_components/Inpixon";
import PageNotFound from "./components/main_components/Pagenotfound";

Vue.use(VueRouter);

let router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "About",
      component: About,
    },

    {
      path: "/portfolio",
      name: "Portfolio",
      component: Portfolio,
    },
    {
      path: "/blog",
      name: "Blog",
      component: Blog,
    },
    {
      path: "/contactus",
      name: "Contactus",
      component: ContactUs,
    },
    {
      path: "/project",
      name: "Projects",
      component: Project,
      redirect: {
        name: "Projects.Inpixon",
      },
      children: [
        {
          alias: "",
          path: "/project/inpixon",
          name: "Projects.Inpixon",
          component: Inpixon,
        },
        {
          path: "/project/bdb",
          name: "Projects.BDB",
          component: BDB,
        },
      ],
    },
    {
      path: "*",
      name: "PageNotFound",
      component: PageNotFound,
    },
  ],
});

export default router;
